.container {
    padding: 30px 20px;
    width: 100%;
    height: 600px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.hero-photo {
    margin-top: 40px;
    width: 80%;
    height: 80%;
    align-self: center;
}

.container-heading {
    font-size: 36px;
    width: 100%;
    text-align: left;
    margin-bottom: 0;
}

.info-p {
    width: 100%;
    text-align: left;
}

.info {
    width: 30%;
}

@media (max-width:480px)
{
    .hero-photo {
        width: 100%;
        height: 20%;
    }
    .info {
    margin-top: 0;
    width: 70%;
    }
    
    .hero-photo {
        margin-top: 0;
        width: 100%;
        height: 100%;
    }
   
}