.cards-container {
    padding: 20px 25px;
    margin-top: 10px;
    display: flex;
    flex-wrap: nowrap;
    flex-direction: row;
}

.card {
    display: flex;
    flex-direction: column;
    width: 242px;
    height: 320px;
}

.currentstatus {
    position: absolute;
    top: 7px;
    left: 10px;
    border: 1px white solid;
    border-radius: 2px;
    background-color: white;
    text-align: center;
    padding: 3px;
    width: 70px;
    height: 25px;
    font-size: 11px;
}

.card-image {
    position: relative;
    
}

.card-image img {
    width: 242px;
}

.card-image {
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}

.popularity-info {
    margin-top: 2px;
    margin-bottom: 2px;
    font-size: 15px;
}

.popularity-info span {
    display: inline;
}

.title p {
    margin-top: 2px;
    margin-bottom: 2px;
}

.pricing {
    width: 100%;
}

.pricing p {
    margin-top: 2px;
    margin-bottom: 2px;
    font-size: 15px;
}

.numofpeoplerated, .country {
    color: #918E9B;
}

.bltpoint {
    vertical-align: middle;
}

.staricon {
    width: 14px;
    height: 14px;
    vertical-align: baseline;
}


